define('settingsReader',['jsonReader'], function (jsonReader) {

    function readTemplateSettings() {
        return jsonReader.read('settings.js?v=1474380865808');
    }

    function readPublishSettings() {
        return jsonReader.read('publishSettings.js?v=1474380865808');
    }

    function readThemeSettings() {
        return jsonReader.read('themeSettings.js?v=1474380865808');
    }

    return {
        readTemplateSettings: readTemplateSettings,
        readPublishSettings: readPublishSettings,
        readThemeSettings: readThemeSettings
    };

});
