define('bootstrapper.tasks',['require','bootstrapping/browserSupportTask','bootstrapping/viewLocatorTask','bootstrapping/knockoutBindingTask','bootstrapping/binderTask','bootstrapping/routingTask','bootstrapping/reviewTask'],function (require) {
    return {
        getCollection: function () {
            return [
                require('bootstrapping/browserSupportTask'),
                require('bootstrapping/viewLocatorTask'),
                require('bootstrapping/knockoutBindingTask'),
                require('bootstrapping/binderTask'),
                require('bootstrapping/routingTask'),
                require('bootstrapping/reviewTask')
            ];
        }
    };
});
