define('viewmodels/introduction',['durandal/app', 'context', 'plugins/router', 'plugins/http'],
    function (app, context, router, http) {

        var courseTitle = null,
            content = null,

            canActivate = function () {
                if (context.course.hasIntroductionContent == false) {
                    return { redirect: '#sections' };
                }
                return true;
            },

            activate = function () {
                this.courseTitle = "\"" + context.course.title + "\"";

                var that = this;
                return Q.fcall(function () {
                    return http.get('content/content.html?v=1474380865808').then(function (response) {
                        that.content = response;
                    }).fail(function () {
                        that.content = '';
                    });
                });

            },

            startCourse = function () {
                if (router.isNavigationLocked()) {
                    return;
                }
                router.navigate('sections');

            };

        return {
            courseTitle: courseTitle,
            content: content,
            isNavigationLocked: router.isNavigationLocked,

            startCourse: startCourse,
            canActivate: canActivate,
            activate: activate
        };
    }
);
